
.protection-score-meter {
  overflow: hidden;
  position: relative;
  
  canvas {
    background-color: transparent;
    position: absolute;
  }

  .score-text-content {
    text-align: center;
    position: absolute;
    bottom: 0;
    padding-bottom: 5px;
  }

  .score-header {
    font-size: 60px;
    font-family: 'Poppins', 'Open Sans';
    font-weight: 700;
    color: #212934;
  }

  .score-subheader {
    margin-top: 10px;
    font-size: 18px;
    font-family: 'Poppins', 'Open Sans';
    font-weight: 600;
    color: #212934;
  }

  .score-tag {
    background: #E2ECFE;
    border-radius: 12px;
    display: inline-block;
    font-size: 16px;
    font-family: 'Poppins', 'Open Sans';
    font-weight: 600;
    margin-top: 10px;
    padding: 4px 12px;
    color: #193F84;
  }
}
